import Vue from 'vue';
import Router from 'vue-router';
import login from '@/components/login';
import index from '@/components/index';//
//import commodity from '@/components/commodity';
//import deletes from '@/components/delete';//
//import updata from '@/components/updata';
//import company from '@/components/company';//
//import management from '@/components/management';//
//import updatafiliale from '@/components/updatafiliale';
//import official from '@/components/official';//
//import updatasites from '@/components/updatasites';//
//import merchant from '@/components/merchant';//
//import updatamerchant from '@/components/updatamerchant';//
//import changemerchant from '@/components/changemerchant';//
//import dynamic from '@/components/dynamic';//
//import updatadynamic from '@/components/updatadynamic';//
//import updynamic from '@/components/updynamic';//
//import news from '@/components/new';
//
import commodityUpload from '@/components/commodityUpload';	//

const personnel=()=>import('@/components/personnel');			//
const StoreType=()=>import('@/components/StoreType');	//
const CommodityType=()=>import('@/components/CommodityType');	//

const productList=resolve=>{
//	console.log('');
	$('#loading').show()
	require.ensure([],()=>{
		resolve(require('@/components/productList'))
//		console.log('')
		$('#loading').hide()
	})
}

Vue.use(Router)
export default new Router({
  routes: [
    {
    	path:'/login',
    	name:'login',
    	component:login,
    },
		{
        path: '/',
//      name:'index'
        component: index,
        children: [
          	{path: '/',meta:{'index':1},component:productList},	//
          	{path: '/index/productList',name:'productList',meta:{'index':1},component:productList},	//
          	{path: '/index/commodityUpload',meta:{'index':2},name:'',component:commodityUpload},	//
         	{path:'/personnel',name:'',component:personnel},	//  
        	{path:'/StoreType',name:'',component:StoreType},	//
        	{path:'/CommodityType',name:'',component:CommodityType}		//
        	
        ]
   	}
  ]
})



